@use 'additional/mixins' as *;

.audio {
  &__inner {
    max-width: 700px;
    margin-inline: auto;
    position: relative;

    &::after {
      content:'';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 9999999;
      margin-inline: auto;
      width: 235px;
      height: 50px;
      border-radius: 100vmax;
      background-color: var(--clr-mainColor);
      text-align: center;
      color: var(--clr-white);
    }
  }
  &__title {
    text-align: center;
    margin-bottom: 40px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 30px;
  }

  &__link {
    border-radius: 10px;
    overflow: hidden;
  }
}
@include breakpoint-down(medium) {
  .audio {
    &__title {
      margin-bottom: 25px;
    }

    &__inner {
      margin-inline: auto;
    }
  }
}
