@use 'additional/mixins' as *;
.footer {
  padding: 50px 0;

  &__title {
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    gap: 20px;
  }

  &__item {
    border-radius: 100vmax;

    &:hover {
      box-shadow: 0 0 20px #ffee10;
    }
  }

  &__link {
    border-radius: 100vmax;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #383d45;
    transition: 0.3s linear;
    position: relative;
    z-index: 2;

    svg {
      width: 27px;
      height: 27px;
      transition: 0.2s linear;
      stroke: #fff;
      fill: #fff;
    }

    &:hover {
      svg {
        fill: #ffee10;
        filter: drop-shadow(0 0 5px #ffee10);
      }
    }
  }
}

@include breakpoint-down(medium) {
  .footer {
    padding: 25px;

    &__title {
      text-align: center;
    }

    &__list {
      justify-content: center;
    }
  }
}

@include breakpoint-down(regular) {
  .footer {
    padding: 25px 0 75px;
  }
}
